<template>
  <div class="app">
    <!-- add csv -->
    <label class="dropzone">
      <span> + CSV </span>
      <input type="file" ref="input" hidden />
    </label>

    <!-- download all -->
    <button v-if="products.length" v-on:click="downloadAll" class="btn">
      download all
    </button>

    <!-- list -->
    <div class="list">
      <div v-for="product in products" :key="product.sku" class="card">
        <Display :product="product" :ref="setRef" />
      </div>
    </div>
  </div>
</template>
<script>
import Papa from 'papaparse';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';
import Display from './components/display';

export default {
  components: {
    Display
  },
  data() {
    return {
      products: [],
      refs: []
    };
  },
  mounted() {
    const input = this.$refs.input;
    input.onchange = () => {
      this.products = [];
      const file = input.files[0];
      if (file) {
        file
          .text()
          .then(data => {
            const options = { header: true, skipEmptyLines: true };
            const parsed = Papa.parse(data, options);
            const products = parsed.data;

            if (
              (parsed.errors && parsed.errors.length) ||
              products.some(product => {
                return (
                  !product.SKU ||
                  !product.Naam ||
                  !product.Price ||
                  !product.Url
                );
              })
            ) {
              throw new Error();
            }

            this.products = products.map(product => {
              return {
                sku: product.SKU,
                url: product.Url,
                name: product.Naam,
                price: product.Price
              };
            });
          })
          .catch(() => {
            alert('Error parsing the file');
          });
      }
    };
  },
  beforeUpdate() {
    this.refs = [];
  },
  methods: {
    downloadAll() {
      const zip = new JSZip();
      this.refs.forEach(ref => {
        const qrcode = ref.toText();
        const fileName = ref.product.name + '.svg';
        zip.file(fileName, qrcode);
      });
      zip.generateAsync({ type: 'blob' }).then(content => {
        saveAs(content, 'QR codes.zip');
      });
    },
    setRef(el) {
      if (el) this.refs.push(el);
    }
  }
};
</script>
